@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300&display=swap);
:root {
  --theme-primary-color:  #9b9ece;
  --theme-secondary-color: #585d99;
  --color-background: lightgray;
}
body {
  margin: 0;
  font-family: "Open Sans", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
ul{
  list-style: none;
}
a{
  text-decoration:none;
}

header {
  background-color: var(--theme-primary-color);
  height: 60px;
  font-size: 1.1em;
  box-shadow: 0px 7px 17px -6px rgba(176, 167, 176, 1);
}
ul{
  padding:0;
}
.header-nav {
  max-width: 1400px;
  width: 100%;
  margin: auto;
}
.header-nav-menu img{
 
  width: 0;
}
.header-nav-menu {
  position: absolute;
}
.header-nav-list {
  height: 60px;
  margin: 0;
  display: grid;
  grid-template-columns: 50% 35% 15%;
  grid-template-rows: 100%;
  justify-items: end;
  align-items: center;
  visibility: visible;
}

.header-nav-login {
  grid-column: 1/3;
}
.header-nav-signup {
  grid-column: 3/4;
  justify-self: start;
}
.header-nav-list li {
  margin: 10px 20px;
}
.header-nav-list li a {
  color: black;
}
.header-div-avatar {
  width: 30px;
  height: 30px;
  margin-right: 10px;
}
.header-div-avatar img {
  border-radius: 100%;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.header-nav-profile {
  display: flex;
  align-items: center;
}

.header-nav-button {
  background-color: transparent;
  color: white;
  border: 1px solid white;
  border-radius: 10px;
  padding: 10px 15px;
  cursor: pointer;
  font-family: "Open Sans", sans-serif;
  font-size: 0.8em;
}
.header-nav-button:hover {
  text-shadow: 0 0 1px #ffffff;
  background-color: #0000000c;
}
@-webkit-keyframes link-hover {
  from {
    color: black;
  }
  to {
    color: rgba(255, 255, 255, 0.445);
  }
}
@keyframes link-hover {
  from {
    color: black;
  }
  to {
    color: rgba(255, 255, 255, 0.445);
  }
}
.header-nav-list li a:hover {
  color: white;
  -webkit-animation-name: link-hover;
          animation-name: link-hover;
  -webkit-animation-duration: 0.2s;
          animation-duration: 0.2s;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}
.active-tab {
  font-weight: 800;
}
.header-nav-menu{
  visibility: hidden;
}
@media only screen and (max-width: 1000px) {

  .header-nav-list-logged .header-nav-list {
    height: 150px;
  }
  .header-nav-menu img {
    width: 25px;
  }
  .header-nav-menu {
    position: relative;
    background: transparent;
    border: none;
    margin-bottom: 20px;
    cursor: pointer;
    visibility: visible;
  }
   .header-nav-mobile {
    padding: 20px 0 0 20px;
  }
  .header-nav-list-logged  {
    padding: 0;
    grid-template-columns: 300px;
    grid-template-rows: 1fr 1fr 1fr;
    justify-items: start;
    align-items: flex-start;
  }
  .header-nav-list-logged li {
    margin-left: 0;
  }
  .header-nav-button {
    color: black;
    border: none;
    cursor: pointer;
    font-family: "Open Sans", sans-serif;
    font-size: 1em;
    padding: 0;
  }
  .header-nav-button:hover {
    color: white;
    -webkit-animation-name: link-hover;
            animation-name: link-hover;
    -webkit-animation-duration: 0.2s;
            animation-duration: 0.2s;
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    text-shadow: none;
    background-color: transparent;
  }
  .hide-menu{
    visibility: hidden;
  }
  .expand-header{
    height: 220px;
  }
  .fade-menu img{
    opacity: 0.5;
  }
}
@media only screen and (max-width: 700px) {
.header-nav-login{
  grid-column:1/2;
}
.header-nav-signup {
 grid-column:2/4; 
}
}
.authentification-form {
  margin: 2em auto;
  background-color: #585d99;
  width: 400px;
  padding: 20px;
  border-radius: 20px;
  color: white;
}
.authentification-form__inputs {
}
.authentification-form__actions {
  margin: 20px 0 10px 0;
  align-items: center;
}
.authentification-form,
.authentification-form__actions,
.authentification-form__inputs {
  display: flex;
  flex-direction: column;
}
.authentification-form__submit {
  background-color: transparent;
  color: white;
  border: none;
  padding: 10px 15px;
  width: 100%;
  cursor: pointer;
  font-family: "Open Sans", sans-serif;
  font-size: 1em;
  border: 1px solid white;
}
.authentification-form__submit:hover {
  background-color: #0000000c;
  text-shadow: 0 0 1px #ffffff;
}
.authentification-form__inputs input {
  border: 1px solid white;
  border-radius: 5px;
  padding: 10px 15px;
  margin-top: 10px;
}

.authentification-form__name h1 {
  text-align: center;
  margin:10px 0;
}
.authentification-form__link {
  color: rgba(255, 255, 255, 0.377);
  margin-top: 10px;
  font-size: 0.8em;
}
.authentification-form__link:hover {
  color: white;
}
.authentification-form__invalid {
  color: rgb(252, 98, 98);
}
.invalid {
  background-color: rgba(255, 0, 0, 0.075);
  border-color: rgb(252, 98, 98) !important;
}
@media only screen and (max-width: 600px) {
  .authentification-form {
    width:80%;
  }
}
.post-form__container {
  border:1px solid var(--theme-secondary-color);
  border-radius: 1%;
  width: 500px;
  margin: 20px auto;
  padding: 20px;
  box-shadow: 0px 7px 17px -6px rgba(176,167,176,1);
}
.post-form__heading {
  display: flex;
  align-items: center;
  margin-bottom:10px;
  color:var(--theme-secondary-color);
  font-weight: 800;
}
.post-form__avatar {
  width: 30px;
  height: 30px;
  margin-right:10px;
}
.post-form__avatar img {
  object-fit: cover;
  border-radius: 100%;
  width: 100%;
  height: 100%;
}
.post-form__form {
  display: flex;
  flex-direction: column;
}
.post-form__submit {
  background-color:var(--theme-primary-color);
  color: white;
  border: none;
  border-radius: 10px;
  padding: 10px 15px;
  cursor: pointer;
  font-family: "Open Sans", sans-serif;
  font-size: 0.8em;
  margin-top: 10px;
}
.post-form__submit:hover {
  text-shadow: 0 0 1px white;
  background-color: #9598c4;
}
.post-form__dropped-file {
  padding: 0;
  margin: 0;
  display: flex;
  flex-wrap: wrap;
  background-color: white;
  width: 200px;
  position: relative;
}

.post-form__dropped-file img {
  margin-top:10px;
  width: 100%;
  height: 200px;
  object-fit: cover;
}
.post-form__dropped-file__undo{
  position: absolute;
  cursor: pointer;
  background:rgba(0, 0, 0, 0.37);
  color:white;
  font-size:17px;
  padding-right:5px;
  border:none;
  width: 100%;
  right:0;
  text-align:right;
  top:10px;
}
.post-form__dropped-file__undo:hover{
  text-shadow: 0 0 1px #ffffff;
}
@-webkit-keyframes message {
  0% {opacity: 0;}
  50%{opacity: 1;}
  100%{opacity: 0;}
}
@keyframes message {
  0% {opacity: 0;}
  50%{opacity: 1;}
  100%{opacity: 0;}
}
.post-form__message{
  position: absolute;
  left:45%;
  top:30%;
  color:white;
  background-color:var(--theme-secondary-color);
  padding:10px;
  border-radius:5px;
  -webkit-animation-name: message;
          animation-name: message;
  -webkit-animation-duration: 4s;
          animation-duration: 4s;
}
@media only screen and (max-width: 600px) {
  .post-form__container {
    width:80%;
  }
}
.drag-drop-zone {
  padding: 10px;
  margin: 0;
  background-color: white;
  resize: none;
  height: 100px;
  width: 100%;
  border: none;
  box-shadow: inset 0px 0px 30px 0px rgba(0, 0, 0, 0.096);
  font-family: "Open Sans", sans-serif; 
  box-sizing: border-box;
}
.drag-drop-zone p {
  color: #fff;
}
.drag-drop-zone.inside-drag-area {
  box-shadow: inset 0px 0px 30px 0px rgba(117, 106, 172, 0.479);
}


.post-list-container{
    margin-left:auto;
    margin-right:auto;
    display: flex;
    justify-content: center;
}
.post-card {
  border: 1px solid var(--theme-secondary-color);
  border-radius: 1%;
  width: 500px;
  margin: 20px auto;
  padding: 20px;
  box-shadow: 0px 7px 17px -6px rgba(176, 167, 176, 1);
  display: flex;
  flex-direction: column;
}
.post-card__likes {
  width: 30px;
  display: flex;
}
.post-card__likes button {
  cursor: pointer;
  background: transparent;
  border: none;
}
.post-card__likes button img{
  width: 20px;
  -webkit-filter: brightness(0%);
          filter: brightness(0%);
}
.liked{
  -webkit-filter: brightness(100%) !important;
          filter: brightness(100%) !important;
}
.post-card__heading,
.post-card__heading a {
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: var(--theme-secondary-color);
  font-weight: 800;
  font-size: 1.1em;
}
.post-card__heading a:hover {
  cursor: pointer;
  color: var(--theme-primary-color);
}
.post-card__text {
  margin: 20px 0;
}
.post-card__image {
  margin: auto;
  max-width: 100%;
  margin-bottom: 20px;
}
.post-card__image img {
  max-height: 450px;
  width: 100%;
  object-fit: cover;
}
.post-card__heading__delete {
  background: transparent;
  border: none;
  font-size: 20px;
  cursor: pointer;
}
.post-card__heading__delete:hover {
  color: rgb(223, 100, 100);
}
@media only screen and (max-width: 600px) {
  .post-card {
    width:80%;
  }
}
.post-list-container{
    margin-left:auto;
    margin-right:auto;
    display: flex;
    justify-content: center;
}
.profile {
  margin: 20px auto;
  padding: 20px;
  background-color: var(--theme-secondary-color);
  width: 550px;
  height: 280px;
  display: flex;
  border-radius: 5px;
  color: white;
  box-shadow: 0px 7px 17px -6px rgba(176, 167, 176, 1);
  --profile-image-size: 170px; /* Safari/Chrome, other WebKit */ /* Firefox, other Gecko */
  box-sizing: border-box;
}
.profile-edit-name-button {
  background-color: transparent;
  border: none;
  cursor: pointer;
}
@-webkit-keyframes edit-button-hover {
  from {
    opacity: 1;
  }
  to {
    opacity: 0.3;
  }
}
@keyframes edit-button-hover {
  from {
    opacity: 1;
  }
  to {
    opacity: 0.3;
  }
}
.profile-edit-name-button img:hover,
.profile-edit-photo-button img:hover {
  -webkit-animation-name: edit-button-hover;
          animation-name: edit-button-hover;
  -webkit-animation-duration: 0.2s;
          animation-duration: 0.2s;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}
.profile-edit-name-button img {
  width: 15px;
}
.profile-image {
  height: var(--profile-image-size);
  width: var(--profile-image-size);
}
.profile-image img {
  height: 100%;
  width: 100%;
  object-fit: cover;
  border-radius: 1%;
  border: 3px solid #9b9ece;
}
.profile-edit-name {
  display: flex;
  margin-bottom: 10px;
  align-items: center;
}
.profile-edit-name input,
.profile-name {
  margin-right: 30px;
  font-size: 20px;
}
.profile-email {
  font-size: 14px;
}
.profile-edit-name input {
  background-color: transparent;
  padding: 0;
  border: none;
  font-family: "Open Sans", sans-serif;
  box-shadow: 0 1px 0 #ffffff;
  color: white;
}
.profile-edit-name-buttons-div {
  display: flex;
}
.profile-edit-name-buttons-div button img,
.profile-edit-photo-buttons-div button img {
  width: 12px;
}
.profile-edit-photo-buttons-div button img {
  border: none;
}
.profile-image-side {
  margin-right: 30px;
  display: flex;
  flex-direction: column;
}
.profile-name-error {
  font-size: 12px;
  color: rgb(252, 98, 98);
}

.profile-image input[type="file"]::file-selector-button,
.profile-chnage-photo-button {
  margin-top: 5px;
  background-color: transparent;
  color: white;
  border: 1px solid white;
  border-radius: 10px;
  padding: 10px 15px;
  cursor: pointer;
  font-family: "Open Sans", sans-serif;
  font-size: 0.8em;
  width: var(--profile-image-size);
}
.profile-image input[type="file"]::file-selector-button:hover,
.profile-chnage-photo-button:hover {
  text-shadow: 0 0 1px #ffffff;
  background-color: #0000000c;
}
.profile-edit-photo-buttons-div {
  border: none;
  margin-top: 10px;
}
.profile-image-photo-edit {
  display: flex;
  flex-direction: column;
}
@media only screen and (max-width: 630px) {
  .profile {
    flex-flow: column-reverse;
    width: 80%; /* Safari/Chrome, other WebKit */    /* Firefox, other Gecko */
    box-sizing:unset; 
    height:auto;
    --profile-image-size: none;
  }
  .profile-image-side {
    margin-top:20px;
    width:100%;
  }
  .profile-edit-name input,
.profile-name {
  font-size: 30px;
}
.profile-image-side  img{
  width:100%;
  height: 80vw;
}
}

